import React, { useEffect, useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom"; 
import { Box, Container, Grid } from "@mui/material";
import { singlePageStyles } from "src/components/styles/customStyles";

const AllCountries = () => {
  const [countries, setCountries] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchCountries = async () => {
      try {
        const response = await axios.post(
          "https://campusroot.com/api/v1/public/listings/destinations"
        );
        setCountries(response.data.data.list);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };
    fetchCountries();
  }, []);


// Navigate to the dynamic route with the ID
  const handleRedirect = (id:string) => {
    navigate(`/country/${id}`); 
  };

  return (
    <Container className="contain" style={{marginTop: "5.5rem",marginBottom:"5.5rem"}}>
      <div className="allCountry-heading">
        <h4>
          Find your <span>Ideal</span> Country
        </h4>
      </div>
      <Grid container >
        {
        countries?
        countries.map((ele:any, index) => (
            <Grid item lg={4} md={6} sm={6} xs={6} key={index}  >
              <div className="all-countries" onClick={() => handleRedirect(ele._id)}>
                <img
                  src={ele?.coverImageSrc}
                  alt={"img"}
                  className="country-image"
                />
                <h5>{ele.title}</h5>
              </div>
            </Grid>
          )
        )
        :
        <Box sx={singlePageStyles.loadingDiv}>Loading details...</Box>
        }
      </Grid >
    </Container>
  );
};

export default AllCountries;